.filter-section {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
  }
  
  .filter-controls {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .filter-controls input,
  .filter-controls select,
  .filter-controls button {
    flex: 1;
    min-width: 150px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .filter-controls button {
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .filter-controls button:hover {
    background-color: #0056b3;
  }
  