.house-listing {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .house-item {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    flex: 1 1 calc(33% - 20px); /* Adjust percentage based on your grid needs */
    box-sizing: border-box;
  }
  
  .image-section {
    width: 100%;
    overflow: hidden;
  }
  
  .slideshow {
    display: flex;
    overflow-x: auto;
  }
  
  .slideshow img {
    max-width: 100%;
    height: auto;
    margin-right: 10px;
  }
  
  .details-section {
    padding: 15px;
  }
  
  .details-section h3 {
    margin: 0 0 10px;
  }
  
  .description {
    margin: 10px 0;
  }
  
  .actions {
    display: flex;
    justify-content: space-between;
  }
  
  .details-link {
    color: #007bff;
    text-decoration: none;
  }
  
  .book-visit-button {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .book-visit-button:hover {
    background-color: #218838;
  }
  