/* src/components/Navbar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 10px 20px;
    color: #fff;
  }
  
  .navbar-left {
    display: flex;
    align-items: center;
  }
  
  .navbar-logo {
    width: 40px;
    height: 40px;
    margin-right: 15px;
  }
  
  .navbar-title {
    font-size: 1.5em;
    font-weight:bold;
  }
  
  .navbar-button {
    background-color: #f39c12;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .navbar-button:hover {
    background-color: #e67e22;
  }
  
  @media (max-width: 768px) {
    .navbar-title {
      font-size: 1.2em;
    }
  
    .navbar-button {
      font-size: 0.9em;
      padding: 8px 12px;
    }
  }
  